<template>
    <div>
        <h1 v-html="article.title"></h1>
        <h4 v-html="article.subtitle"></h4>
        <p v-html="article.content"/>
    </div>
</template>

<script>
import { db } from '../db'

export default {
    data() {
        return {
            article: {}
        }
    },
    // mounted() {
    //     db.collection('documents')
    //     .doc(this.$route.params.id)
    //     .get()
    //     .then(snapshot => {
    //         const document = snapshot.data()
    //         this.article = document
    //     })
    // },
    firestore() {
        db.collection('documents')
        .doc(this.$route.params.id)
        .onSnapshot(snap => {
            const document = snap.data()
            this.article = document
        })
    }
}
</script>