<template>
  <div class="home">
    <ul v-if="documents.length">
      <li v-for="(article, index) in documents" :key="index">
        <router-link :to="`/articles/${article.id}`">{{article.title}}</router-link>
      </li>
    </ul>
    
    <CatDetails :article="documents[0]"/>
    <!-- <Article v-if="documents.length" :article="documents[0]"/> -->
    <CatDetails msg="Welcome to Your Vue.js App"/>
  </div>
</template>

<script>
// @ is an alias to /src
import CatDetails from '@/components/CatDetails.vue'
// import Article from '@/components/Article.vue'
import { db } from '../db'

export default {
  name: 'home',
  components: {
    CatDetails
    // ,Article
  },
  data() {
    return {
      documents: []
    }
  },
  mounted() {
    db.collection('documents')
      .get()
      .then((querySnapshot) => {
        const docs = querySnapshot.docs.map(doc => {
          const {title, subtitle, content} = doc.data()
          const adoc = {id: doc.id, title, subtitle, content}
          return adoc
        })

        this.documents = docs
      })
  }
}
</script>
